.footer-container {
    background: #3f4e65;
    color: white;
    white-space: nowrap;
}

.footer-wrap {
    width: 70%;
    margin: 0 auto;

    gap: 95px;

    box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
    padding: 70px;
}

.footer-headline {
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 25px;
}

.logo-img {
    margin-bottom: 35px;
}

.input-wrap {
    background: white;
    max-width: 282px;
    padding: 11px 10px 11px 20px;
    margin-top: 15px;
}

.arrow-btn {
    width: 39px;
    height: 39px;
}

.price-list-btn {
    display: flex;
    padding: 10px 19px;
    gap: 12px;
}
.white-text {
    margin-bottom: 20px;
}

.first-col {
    gap: 45px;
}


.social-btn-wrap{
    gap:10px;
    margin-top: 15px
}